.col-md-4, .col-sm-4{
    margin-top: 30px;
}
.colDarkBg{
    background-color: black;
    color: white;
    padding: 20px;
}
.colLightBorder{
    border: 1px solid gray;
    box-shadow: 2px 5px 10px 6px;
}
h1,h2,h3,h4,h5,h6,a{
    font-family: 'Adamina', serif;
}
p,span{
    font-family: 'Open Sans', sans-serif;
}
#popupUpdate{
    display: none;
} 
.topSpacing{
    margin-top: 20px;
    margin-bottom: 20px;
}
.bg-navBar{
    background-color: black !important;
}
.bg-navBar a{
    color: white !important;   
    font-weight: bold;
}

.nav-link:hover{
    color:lightblue !important;
}
.topbar{
    margin:0 0 0 0;
}
#category-nav a{
    font-size: 16px;
}
.bg-navBar{
    border-radius: 0 !important;
}
.topBarIcons{
    text-align: right;
    padding:15px 10px 0 0;
}
.topBarIcons i{
    color:white;
    margin-right: 10px;
    font-size: 16px;
}
.custom-toggler,.navbar-toggler {
    border-color: white !important;
} 
.custom-toggler,.navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 0.7)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E") !important;
}
@media only screen and (max-width: 768px) {
    .topBarIcons,
    .topbar{
        text-align: center;
    }
    .navbar-brand{
        float: none;
    }
    .topBarIcons i{
        padding:0 0 10px 0;
    }
}
@media (min-width: 768px){
    .navbar-collapse.collapse {
        display: inherit !important;
    }
}
.tobarCustomPadding{
    padding:0 !important;
}

.navbar-brand {
    color: white;
}

.navbar {
    position: -webkit-sticky;
    position: sticky;
}

.nav-link * {
    font-family: 'Adamina', serif !important; 
}

.postCategory{
    width: 100%;
}
